'use strict';

import youtube from 'youtube-iframe-player';
import Controls from './Controls';


export default function VideoPlayer(
    playerElementId,
    progressElementSelector,
    volumeElementSelector,
    currentTimeElementSelector,
    durationElementSelector) {

    Controls.toggleControls();

    const main = document.querySelector('#main');
    const element = document.createElement('div');
    element.setAttribute("class", playerElementId);
    element.setAttribute("id", playerElementId);
    main.prepend(element);

    this.playerReady = (e) => {
        this.player.setVolume(100);
    };

    this.onPlayerStateChange = (e) => {

    }

    youtube.init(() => {

            this.player = new youtube.createPlayer('video-player', {
                height: 0.56 * main.getBoundingClientRect().width,
                width: main.getBoundingClientRect().width,
                videoId: 'y5p8qdiAXSo',
                playerVars: {
                    'autoplay': 0,
                    'controls': 0,
                    'fs': 1,
                    'modestbranding': 1,
                    'rel': 0,
                    'start': 1,
                },
                events: {
                    'onReady': this.playerReady,
                    'onStateChange': this.onPlayerStateChange,
                }
            });
        }
    );

    const progressElement = document.querySelector(progressElementSelector);
    const volumeElement = document.querySelector(volumeElementSelector);

    this.progressController = new Controls(progressElement, this, 'currentTime');
    this.volumeController = new Controls(volumeElement, this, 'volume');

    this.currentTimeElement = document.querySelector(currentTimeElementSelector);
    this.durationElement = document.querySelector(durationElementSelector);

    this.isPlaying = false;

    Object.defineProperties(this, {
            volume: {
                configurable: false,
                enumerable: false,
                // 1 - 100
                set: function (value) {
                    this.player.setVolume(value * 100);
                }
            },
            currentTime: {
                configurable: false,
                enumerable: false,
                get: function () {
                    return this.player.getCurrentTime();
                },
                // seconds
                set: function (value) {
                    this.player.seekTo(this.duration * value);
                }
            },
            duration: {
                configurable: false,
                enumerable: false,
                get: function () {
                    return this.player.getDuration();
                }
            },
        }
    );

    this.play = () => {
        if (this.isPlaying) {
            this.player.pauseVideo();
            this.isPlaying = false;
        } else {
            this.player.playVideo();
            this.progressController.watchCurrentTime(this);
            this.isPlaying = true;
        }
    };

    this.deletePlayer = () => {
        let element = document.querySelector('iframe');
        if (element) {
            element.remove();
        }
        Controls.toggleControls();
    }
};