'use strict';

import {encode} from 'js-base64';

export default function Mailer(sender, password) {

    toggleFormFields();

    this.sender = sender;
    const pass = password;

    function hideOrShowMailerElement() {
        document.querySelector('#mailer').classList.toggle('mailer--active');
    }

    hideOrShowMailerElement();

    function disableInputs() {
        document.querySelectorAll('.form__input')
            .forEach(element => element.setAttribute('readonly', 'true'));
    }

    function enableInputs() {
        document.querySelectorAll('.form__input')
            .forEach(element => element.removeAttribute('readonly'));
    }

    disableInputs();

    function appendGoogleScripts() {
        const app = document.querySelector('#app');

        const apisScript = document.createElement('script');
        apisScript.setAttribute('id', 'apis');
        apisScript.setAttribute('src', 'https://apis.google.com/js/api.js');

        const gsiScript = document.createElement('script');
        gsiScript.setAttribute('id', 'gsi');
        gsiScript.setAttribute('src', 'https://accounts.google.com/gsi/client');
        app.before(gsiScript);
        app.before(apisScript);
    }

    function removeGoogleScripts() {
        document.querySelector('#apis').remove();
        document.querySelector('#gsi').remove();
    }

    appendGoogleScripts();

    const CLIENT_ID = '198265400971-m6velgm0bo64dhafc81vl2l01lf1gefb.apps.googleusercontent.com';
    const API_KEY = 'AIzaSyD0D9CJX62TmwDpGrd19y0Jw7jxgfzfOqQ';

// Discovery doc URL for APIs used by the quickstart
    const DISCOVERY_DOC = 'https://www.googleapis.com/discovery/v1/apis/gmail/v1/rest';

// Authorization scopes required by the API; multiple scopes can be
// included, separated by spaces.
    const SCOPES = 'https://www.googleapis.com/auth/gmail.send';
    let tokenClient;
    let gapiInited = false;
    let gisInited = false;

    document.getElementById('authorize_button').style.visibility = 'hidden';
    document.getElementById('signout_button').style.visibility = 'hidden';

    /**
     * Callback after api.js is loaded.
     */
    this.gapiLoaded = () => {
        gapi.load('client', this.initializeGapiClient);
    }

    /**
     * Callback after the API client is loaded. Loads the
     * discovery doc to initialize the API.
     */
    this.initializeGapiClient = async () => {
        await gapi.client.init({
            apiKey: API_KEY,
            discoveryDocs: [DISCOVERY_DOC],
        });
        gapiInited = true;
        this.maybeEnableButtons();
    }

    /**
     * Callback after Google Identity Services are loaded.
     */
    this.gisLoaded = () => {
        tokenClient = google.accounts.oauth2.initTokenClient({
            client_id: CLIENT_ID,
            scope: SCOPES,
            callback: '', // defined later
        });
        gisInited = true;
        this.maybeEnableButtons();
    }

    /**
     * Enables user interaction after all libraries are loaded.
     */
    this.maybeEnableButtons = function () {
        if (gapiInited && gisInited) {
            document.getElementById('authorize_button').style.visibility = 'visible';
        }
    }

    /**
     *  Sign in the user upon button click.
     */
    this.handleAuthClick = function () {
        tokenClient.callback = async (resp) => {
            if (resp.error !== undefined) {
                throw (resp);
            }
            document.getElementById('signout_button').style.visibility = 'visible';
            document.getElementById('authorize_button').innerText = 'Refresh';
            enableInputs();

        };
        if (gapi.client.getToken() === null) {
            // Prompt the user to select a Google Account and ask for consent to share their data
            // when establishing a new session.
            tokenClient.requestAccessToken({prompt: 'consent'});
        } else {
            // Skip display of account chooser and consent dialog for an existing session.
            tokenClient.requestAccessToken({prompt: ''});
        }
    }

    /**
     *  Sign out the user upon button click.
     */
    this.handleSignoutClick = function () {
        const token = gapi.client.getToken();
        if (token !== null) {
            google.accounts.oauth2.revoke(token.access_token);
            gapi.client.setToken('');
            document.getElementById('content').innerText = '';
            document.getElementById('authorize_button').innerText = 'Authorize';
            document.getElementById('signout_button').style.visibility = 'hidden';
            disableInputs();
        }
    }

    document.querySelector('#apis').onload = this.gapiLoaded;
    document.querySelector('#gsi').onload = this.gisLoaded;
    document.querySelector('#authorize_button').onclick = this.handleAuthClick;
    document.querySelector('#signout_button').onclick = this.handleSignoutClick;


    function makeEmailMessage(to, from, subject, messageText) {

        const str = [
            'Content-Type: text/plain; charset="UTF-8"\n',
            'to: ', to, '\n',
            'from: ', from, '\n',
            'subject: =?utf-8?B?', encode(subject, true), '?=\n\n',
            messageText
        ].join('');

        return encode(str, true);
    }

    const setConfirmationMessage = function () {
        const errorBlock = document.querySelector('#form__error');
        const color = errorBlock.style.color;

        function deleteErrorMessage() {
            errorBlock.innerHTML = '';
            errorBlock.classList.remove('blink');
            errorBlock.style.color = color;
        }

        errorBlock.style.color = 'teal';
        errorBlock.innerHTML = 'Message sent';
        errorBlock.classList.add('blink');
        setTimeout(deleteErrorMessage, 3000);
    }

    function sendEmailMessage(from) {

        const to = document.querySelector('#to').value;
        const subject = document.querySelector('#subject').value;
        const message = document.querySelector('#message').value;

        const encodedMessage = makeEmailMessage(to, from, subject, message);

        gapi.client.gmail.users.messages.send({
            'userId': 'omnenet@gmail.com',
            'resource': {
                'raw': encodedMessage,
            }
        }).execute(res => {
            if (res.labelIds.includes('INBOX')) {
                setConfirmationMessage();
            }
        });
    };


    function toggleFormFields() {
        document.querySelectorAll('.form__field').forEach(element => element.classList.toggle('form__field--active'));
        document.querySelector('.help').classList.toggle('help--active');
    }

    this.deleteMailer = function () {
        removeGoogleScripts();
        toggleFormFields();
        setTimeout(hideOrShowMailerElement, 3000);
    }

    const validateEmail = function () {
        const input = document.querySelector('#to');
        const errorBlock = document.querySelector('#form__error');

        const validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

        function deleteErrorMessage() {
            errorBlock.innerHTML = '';
            errorBlock.classList.remove('blink');
        }

        function addErrorMessage(isError = true) {
            if (isError) {
                errorBlock.innerHTML = 'Invalid email';
                errorBlock.classList.add('blink');
            } else {
                errorBlock.style.color = 'teal';
                errorBlock.innerHTML = 'Message sent';
                errorBlock.classList.add('blink');
                setTimeout(deleteErrorMessage, 3000);
            }
        }

        if (input.value.match(validRegex)) {
            deleteErrorMessage();
            input.focus();
            return true;
        } else {
            addErrorMessage();
            input.focus();
            setTimeout(deleteErrorMessage, 3000);
            return false;
        }
    }
    document.querySelector('#form').onsubmit = e => {
        e.preventDefault();
    }
    document.querySelector('#send-btn').onclick = () => {
        if (validateEmail()) {
            sendEmailMessage(this.sender);
        }
    };


    const copyPass = () => {
        navigator.clipboard.writeText(pass)
            .then(() => {
                console.log('password copied');
            })
            .catch(err => {
                console.log('Something went wrong', err);
            });
    }

    const copyEmail = () => {
        navigator.clipboard.writeText(this.sender)
            .then(() => {
                console.log('email copied');
            })
            .catch(err => {
                console.log('Something went wrong', err);
            });
    }

    document.querySelector('#copy__password').onclick = copyPass;
    document.querySelector('#copy__email').onclick = copyEmail;

}