'use strict';

import AudioPlayer from './modules/audioPlayer';
import VideoPlayer from './modules/videoPlayer';
import Mailer from "./modules/mailer";

let videoPlayer;
let audioPlayer;
let mailer;

const controlsSelectors = [
    '#controls__play-btn',
    '#controls__next-btn',
    '#controls__prev-btn',
    '#controls__progress-bar__handle',
    '#controls__volume__handle',
    '#controls__time__current',
    '#controls__time__duration'
]

const audioPlayerParams = [
    '#audio-player',
    ...controlsSelectors.slice(3)
];

const cells = document.querySelectorAll(".cell");
const mainCell = document.querySelector("#main-cell");

const audioCell = document.querySelector('#cell--audio-btn');
const videoCell = document.querySelector('#cell--video-btn');
const mailCell = document.querySelector('#cell--mail-btn');

function moveCells() {
    cells.forEach(element =>
        element.classList.toggle('moved')
    );
}

function destroyModules() {
    document.querySelector(controlsSelectors[0]).onclick = null;
    document.querySelector(controlsSelectors[1]).onclick = null;
    document.querySelector(controlsSelectors[2]).onclick = null;

    if (audioPlayer) {
        audioPlayer.deletePlayer();
        audioPlayer = null;
    }

    if (videoPlayer) {
        videoPlayer.deletePlayer();
        videoPlayer = null;
    }

    if (mailer) {
        mailer.deleteMailer();
        mailer = null;
    }
}

function createVideoPlayer() {
    moveCells();
    const videoPlayerParams = controlsSelectors.slice(3)
    videoPlayer = new VideoPlayer('video-player',
        ...videoPlayerParams);

    document.querySelector(controlsSelectors[0]).onclick = videoPlayer.play;
}

function createAudioPlayer() {
    moveCells();
    audioPlayer = new AudioPlayer(...audioPlayerParams);

    document.querySelector(controlsSelectors[0]).onclick = audioPlayer.play;
    document.querySelector(controlsSelectors[1]).onclick = audioPlayer.playNext;
    document.querySelector(controlsSelectors[2]).onclick = audioPlayer.playPrev;

}

function createMailer() {
    moveCells();
    mailer = new Mailer('omnenet@gmail.com','kyswek-meqdu6-zyhHub');
}

videoCell.onclick = createVideoPlayer;
audioCell.onclick = createAudioPlayer;
mailCell.onclick = createMailer;

mainCell.onclick = (event) => {
    if (event.currentTarget.classList.contains('moved')) {
        moveCells();
        destroyModules();
    }
}
